.navbar {
  background-color: #a0c49d;
  display: flex;
  justify-content: space-between;
  padding: 0 2rem;
  align-items: center;
}
.navbar a {
  text-decoration: none;
  color: #393838;
}
.link {
  font-size: 160%;
}
.link::after {
  content: "";
  transition: 0.2s;
  display: block;
  transform: scaleX(0);
  width: 100%;
  height: 2px;
  background-color: #f7ffe5;
  border-radius: 2px;
}
.link:hover::after {
  transform: scaleX(1);
}
@media screen and (max-width: 850px) {
  .navbar {
    flex-direction: column;
    gap: 1rem;
    padding-bottom: 1rem;
  }
}
@media screen and (max-width: 529px) {
  .navbar {
    padding: 0 1rem;
  }
}
