.theme-control {
  display: flex;
  gap: 1rem;
  justify-content: space-between;
  padding: 1rem 2rem;
}

.mode-wrapper img {
  cursor: pointer;
  width: 40px;
  height: 40px;
}
.dot {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #313131;
  cursor: pointer;
  margin: 0 0.5rem;
}
.dots-wrapper {
  display: flex;
  justify-content: flex-end;
}
.active {
  position: relative;
  transition: box-shadow 0.5s ease;
}
.active::after {
  content: "A";
  position: absolute;
  top: 37%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  animation: 1s fadein ease;
}
@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@media screen and (max-width: 850px) {
  .theme-control {
    padding: 1rem 1rem;
  }
}
