.card {
  box-shadow: 1px 1px 4px #656565;
  padding: 0.5rem 1rem;
  display: flex;
  flex-direction: column;
  transition: 1s;
  border-radius: 10px;
}
.card a {
  text-decoration: none;
  padding: 1rem;
  background-color: #2b2a2a;
  width: max-content;
  color: #fff;
  border-radius: 6px;
  transition: 0.2s;
}
.card a:hover {
  opacity: 0.9;
}
.card h2 {
  color: #713abe;
  transition: 0.5s;
}
