.searchbar {
  width: 100%;
  max-width: 400px;
}
.searchbar input {
  margin-left: 1rem;
  padding: 0.5rem;
  flex: 1;
  border: none;
  min-width: 10px;
}
.searchbar form {
  display: flex;
  align-items: center;
  max-width: 400px;
  outline: none;
}
.searchbar form input:focus {
  outline: none;
  box-shadow: inset 1px 1px 1px #908f8f;
}
.searchbar label {
  font-size: 1.2rem;
}
.btn {
  padding: 0.5rem 1rem;
  background-color: #2b2a2a;
  width: max-content;
  color: #fff;
  border-radius: 6px;
  transition: 0.2s;
  outline: none;
  border: none;
  cursor: pointer;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.btn:hover {
  opacity: 0.9;
}
