.create_form {
  background-color: #c4d7b2;
  margin: 2.5rem auto;
  max-width: max-content;
  padding: 1rem 2rem;
  border-radius: 5px;
  align-items: center;
}

.create_form input {
  padding: 0.5rem 0.7rem;
  border: none;
  border-radius: 3px;
}
.method_label {
  display: flex;
  flex-direction: column;
}
.create_form label {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  width: 100%;
}
.method_label textarea {
  resize: none;
}
.create_form label p {
  font-size: 0.9rem;
  color: #4c4c4c;
  margin-top: -5px;
}
.create_form button {
  padding: 0.8rem 1rem;
  background-color: #2b2a2a;
  width: max-content;
  color: #fff;
  border-radius: 6px;
  transition: 0.2s;
  border: none;
  cursor: pointer;
  margin-top: 1rem;
}
.create_form button:hover {
  opacity: 0.9;
}
.ingredients {
  display: flex;
}
.ingredients input {
  width: 100%;
}
.ingredients button {
  margin-right: auto;
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
  padding: unset;
  padding-right: 1rem;
  margin: 0;
  height: 40px;
  padding-left: 1rem;
}
